.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.container{
    background: rgba(60, 186, 214, 80, 0.6); 
    /* opacity: 0.6; */
    /* filter: alpha(opacity=60); */
  border-radius: 25px;

  /* margin: 30px; */
  /* background-color: #ffffff; */
}

.rounded{
  border-radius: 25px;
}

.wrapper {
  /* background: url("assests/bg.jpg") center center no-repeat; */
  background-color: #d2fafb;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
  height: 90vh;
  background: url("assests/title-bg.png") center center no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;

}

.weather-form {
  background: #3cbad6;
  height: 90vh;
  padding-top: 20%;
  padding-left: 50px;
}

.main {
  height: 90vh;
  background: #3cbad6;
  
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  width: 80%;
  margin: 0 auto;
  border-radius: 25px;

}

.title-title{
font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;

}

.title-desc{
  font-style: italic;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
}

.list-group-flush .list-group-item {
    border-right: 0;
    border-left: 0;
    border-radius: 0;
    background-color: #d2fafb;
}

.card-header {
    padding: .75rem 1.25rem;
    margin-bottom: 0;
    background-color: rgba(0,0,0,.03);
    border-bottom: 1px solid rgba(0,0,0,.125);
    background-color:beige;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #ffffff;
  width: 30%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  justify-content: space-between;
  letter-spacing: 2px;
  margin-bottom: 30px;
  /* margin-right: 20px; */
  font-size: 20px;
}

input[type="text"] { 
    outline: none;
  color: rgb(8, 0, 0) !important;
    
}